// FORMULÁRIO
.titleSection {
    display: block;
    background-color: $roxoBackground;
    margin: 0;
    width: 100%;
    //margin-top: 20px;
    border-radius: 20px 20px 0px 0px;
    position: relative;
    margin-bottom: -15px;
    
    
}


.dadosPessoais {

    display: grid;
    width: 60rem;
    position: relative;
    padding: 1rem;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    align-content: center;
    align-items: center;
    gap: 1rem;
    
    
    

    /* font-style: italic; */
    font-weight: bold;
    font-size: large;
            p {
                align-content: center;
                margin-left: 10px;
            }
            h1 {
                text-align: center;
                //background-color: $roxoFundoForm;
                padding: 1rem;
                color: rgb(248, 247, 246);
                
            }
            
            .nameComplete {
                display: flex;
                flex-direction: row;
                gap: 1rem;
                flex-wrap: wrap;
                
                justify-content: space-around;
                    #inputNameComplete{
                        width: 20rem;
                    }

                    #inputCPF{
                        width: 10rem;
                    }

                    #inputCartaoSUS{
                        width: 15rem;
                    }

                    #inputRG{
                        width: 8rem;
                    }

            }
            
            .father1 {
                display: flex;
                flex-direction: row;
                background-color: $roxoFundoForm;
                border-radius: 10px;
                flex-wrap: wrap;
                justify-content: space-around;
                padding-bottom: 20px;
                gap: 2rem;
                box-shadow: 2px 2px 10px rgba(24, 24, 24, 0.788);
                border-radius: 20px 20px 20px 20px;
            }

             .father2{
                display: flex;
                flex-direction: row;
                background-color: $roxoFundoForm;
                border-radius: 10px;
                flex-wrap: wrap;
                justify-content: space-between;
                padding: 20px;
                gap: 2rem;
                box-shadow: $boxShadow;
                

                    h2{
                        width: auto;
                        margin-left: 200px;
                        
                       
                    }

                    #inputNameResp{
                        width: 20rem;
                    }

                    #inputCPFResp{
                        width: 10rem;
                    }

                    #inputRGResp{
                        width: 8rem;
                    }

                    #inputRua{
                        width: 20rem;
                    }

                    #inputNumero{
                        width: 7rem;
                    }

                    #inputCidade{
                        width: 8rem;
                    }



            }

            

    
}

// ESTILO DO INPUT 
input {
    
    height: 1.5rem;
    /* background-color: $verdeFundoNav; */
    border: none;
    border-radius: 20px;
    padding: 10px;

}

input:focus {
    outline: 0;
}

.father {
    display: flex;
    width: 25rem;
}


#btnCadastro {
    color: $roxoBackground;
    background-color: $verdeFundoNav;
    border: none;
    width: 12rem;
    height: 3rem;
    border-radius: 10px;
    margin-left: 22rem;
    margin-right: 20rem;
    margin-top: 2rem;  
    font-weight: bold;
    font-size: 1rem;
    padding: 0.5rem;
    box-shadow: $boxShadow;
    position: relative;
    

           
}

#btnCadastro:hover{
    background-color: $verdeFundoNav;
    transition: 100ms;
    color: $roxoBackground;
    font-weight: bold;
    font-size: 18px;
    box-shadow: 2px 2px 20px 1px rgba(117, 117, 116, 0.678);
    
    
}   


/*FOOTER*/

.main_footer_container {
    /* display: inline; */
    position: relative;
    margin-top: 20px;
}

.botao:hover::after, .botao:hover::after{
    opacity:1;
    width: 150%;
}
.main_footer_copy   { 
    background-color: $verdeFundoNav; 
    width:100%; 
    float:left; 
    font-family: 'Lato', sans-serif; 
    text-align:center; 
    padding:1.5em 0em; 
}

.main_footer_copy p { 
    font-size:1em; 
    color: $roxoBackground; 
    height:20px; 
}
.main_footer_copy .m-b-footer   { 
    margin-bottom:0.5em; 
}
.main_footer_copy .by   { 
    font-size:0.96em; 
}
.main_footer_copy .by a       
{ color:$roxoBackground; 
    text-decoration:none; 
    font-weight:600; 
    transition:0.2s; 
}
.main_footer_copy .by a:hover { 
    text-decoration:underline; 
    color:#313131 
}
.main_footer_copy .by i { 
    font-size:0.96em; 
    margin-right:3px; 
    color:#e82626; 
}
