/////////////////////////////// PÁGINA RELATÓRIOS \\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\


.relatoriosMain {
    display: grid;
    grid-template-columns : 1fr;
    grid-template-rows: 1fr;
    grid-column-gap: 1.5rem;
    grid-row-gap: 0px;
    width: auto;
    height: 80px;
    //background-color: #fca7a7;
    padding: 1rem;
    margin: 1rem;
    align-self: center;
    text-align: center;
    align-items: center;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    color: #3b3b3b;
}


.listaRelatorio {
    display: grid;
    //grid-template-columns : repeat(1,1fr);
    //grid-template-rows: 1fr;
    //grid-column-gap: 1.5rem;
    //grid-row-gap: 0px;
    width: auto;
    height: 45rem;
    //background-color: #e82626;
    margin: 0.5rem;
    padding: 0.5rem;
    margin-left: 15%;
    
    

        .reg1, .reg2 {
            display: grid;
            margin: 10px;
            border-radius: 1rem 0px 0px 1rem;
            overflow-y: scroll;
            flex-wrap: wrap;
            box-shadow: $boxShadow;
            width: 82%;
            right: 120%;

            .qntPac {
                display: grid;
                grid-template-columns: 3fr 2fr 2fr;
                column-gap: 30px;
                row-gap: 10px;
                flex-wrap: wrap;
                width: auto;
                height: 14rem;
                background-color: #e8f8e3;
                margin: 1rem;
                border-radius: 1rem;
                padding: 1rem;
                font-size: 18px;
                overflow-y: wrap;
                font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif
            }

////////////////////    ESTILIZAÇÃO DE BOTÕES { EDITAR E IMPRMIR }///////////////////////////////////////////////////////////////////////////////////////////////////
            .buttonEdit {
                position: relative;
                display: flex;
                //background-color: #e82626;
                justify-content: space-around;
                //text-align: center;
                //align-items: center;
                width: 7rem;
                height: 2.5rem;
                margin-left: 30%;
                margin-right: 30%;
                margin-top: 0.5rem;
                
                cursor: pointer;
                

                    .btnEditReg{
                        background-color: $verdeFundoNav;
                        color: $roxoBackground;
                        display: flex;
                        border: none;
                        border-radius: 10px;
                        width: 7rem;
                        font-size: 18px;
                        text-align: center;
                        align-items: center;
                        justify-content: center;
                        cursor: pointer;
                    }
                    .material-symbols-outlined {
                        padding-left: 5px;
                        position: relative;
                    }
            }

            .btnEditReg:hover{
                background-color: $verdeFundoNav;
                color: $roxoBackground;
                box-shadow: $boxShadow;
                border: none;
                border-radius: 10px;
                width: 7rem;
                font-size: 20px;
                transition: 100ms;
                
            }



            .buttonImprime {
                position: relative;
                display: flex;
                justify-content: space-around;
                text-align: center;
                width: 8rem;
                height: 2.5rem;
                margin-left: 30%;
                margin-right: 30%;
                margin-top: 0.5rem;
                cursor: pointer;

                    .btnImprimeReg{
                        background-color: $verdeFundoNav;
                        color: $roxoBackground;
                        display: flex;
                        border: none;
                        border-radius: 10px;
                        width: 8rem;
                        font-size: 18px;
                        text-align: center;
                        align-items: center;
                        justify-content: center;
                        cursor: pointer;
                        
                        .material-symbols-outlined {
                            padding-left: 5px;
                            position: relative;
                        }
                        
                    }
            }

            .btnImprimeReg:hover{
                background-color: $verdeFundoNav;
                color: $roxoBackground;
                box-shadow: $boxShadow;
                border: none;
                border-radius: 10px;
                width: 8rem;
                font-size: 19px;
                transition: 100ms;
                
                
            }
/////////////////////////////////////////// FIM DE ESTILIZAÇÃO DE BOTÕES { EDITAR E IMPRMIR }  //////////////////////////////////////////////////////////////////////

            .titleReg2, .titleReg1{
                display: flex;
                background-color: $roxoBackground;
                border-radius: 15px 0px 0px 0px;
                font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
                align-items: center;
                font-size: 30px;
                height: 3rem;
                justify-content: center;
                color: rgb(248, 247, 246);
                
                
            }

            .editPac {
                display: grid;
                width: auto;
                height: 35rem;
                margin: 2rem;
                border-radius: 1rem;
                background-color: #e8f8e3;
                
                
            }

            
        }
}






